.photo-container{
  display: block;
  position: relative;
  padding-top: 130px;
  background-image: linear-gradient(to top, rgb(0,0,0),rgb(128,0,0));
  /*padding: 5px 200px;*/
  z-index:1;
}


.photo-galery{
  width: 100%;
  padding: 0px, 10px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  justify-content: space-between;
}

.photo-galery img{
  width: 30%;
  padding: 5px;
}
